*{
    font-family: Georgia, 'Times New Roman', Times, serif;
}

body{
    padding: 60px;
    background: black;
    color: white;
    
}

input{
    background: whitesmoke;
    color: crimson;
    font-size: 1.3rem;
    width: 50%;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    

}

.card p {
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}